<template>
  <div class="pay bg-white brace-fixed">
    <div class="delivery-address border-top-sm">
      <div class="icon">
        <img v-lazy="adIcon" alt />
      </div>
      <div class="user">
        <div class="name">
          <div>李啦啦</div>
          <div>18737528967</div>
        </div>
        <div class="address">广东省 深圳市 南山区 桃源街道 桃花小苑B708</div>
      </div>
      <div class="right-icon">
        <img v-lazy="rightIcon" alt />
      </div>
    </div>
    <div class="buy-order-info border-top-sm">
      <ul class="goods-list">
        <li v-for="item in 10" :key="item">
          <div class="product">
            <img src alt />
          </div>
          <div class="product-info">
            <div class="name">
              商品名称复古宫廷风台灯具灯罩，可 以换行，最多两行
            </div>
            <div class="spec">
              <div>属性:蓝色（不包含灯泡）</div>
              <div>尺码：中码</div>
            </div>
            <div class="price">
              <div>￥799/件</div>
              <div>
                <van-stepper v-model="step[item]" />
              </div>
            </div>
          </div>
        </li>
      </ul>
      <ul class="ext padding-lr">
        <li>
          <div>商品合计</div>
          <div>￥ 28,379.85</div>
        </li>
        <li>
          <div>支付后拼团进度</div>
          <div>80%</div>
        </li>
      </ul>
    </div>
    <div class="border-top-sm">
      <van-cell title="平台优惠" is-link>
        <template #right-icon>
          <div class="discount">
            <div class="price-tag">500元邮寄优惠券</div>
            <img v-lazy="rightIcon" alt />
          </div>
        </template>
      </van-cell>
    </div>
    <div class="pay-type border-top-sm">
      <van-radio-group v-model="radio" checked-color="#F14745" icon-size="23">
        <van-cell-group checked-color="#fff" :border="false">
          <van-cell clickable @click="radio = '1'" :border="false">
            <!-- 使用 title 插槽来自定义标题 -->
            <template #title>
              <img v-lazy="wxIcon" alt />
              <span class="padding-left-sm">微信</span>
            </template>
            <template #right-icon>
              <van-radio name="1" />
            </template>
          </van-cell>
          <van-cell clickable @click="radio = '2'">
            <template #title>
              <img v-lazy="zfbIcon" alt />
              <span class="padding-left-sm">支付宝</span>
            </template>
            <template #right-icon>
              <van-radio name="2" />
            </template>
          </van-cell>
        </van-cell-group>
      </van-radio-group>
      <div class="look-all" @click="openAll">
        {{ lookAll ? "收起全部" : "展开全部" }}
        <img v-lazy="allIcon" alt :class="[lookAll ? 'bottom' : '']" />
      </div>
    </div>
    <div class="fixed-btn">
      <van-submit-bar
        :price="3050"
        button-text="提交订单"
        @submit="onSubmit"
        text-align="left"
      />
    </div>
  </div>
</template>

<script>
import { Stepper, RadioGroup, Radio, Cell, CellGroup, SubmitBar } from "vant";
export default {
  data() {
    return {
      adIcon: require("./address@2x.png"),
      rightIcon: require("@/assets/more@2x.png"),
      wxIcon: require("./wx@2x.png"),
      zfbIcon: require("./zfb@2x.png"),
      allIcon: require("@/assets/all@2x.png"),
      step: {},
      radio: "1",
      lookAll: false
    };
  },
  components: {
    [SubmitBar.name]: SubmitBar,
    [Stepper.name]: Stepper,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup
  },
  methods: {
    openAll() {
      this.lookAll = !this.lookAll;
    }
  }
};
</script>

<style lang="scss">
$addressColor: #888888;
$p-borderAllColor: #efefef; //展开全部边框色值
$p-AllColor: #aeaeae; //展开全部边框色值
.pay {
  //地址
  .delivery-address {
    @include flex;
    position: relative;
    overflow: hidden;
    padding: 39px 0 42px;
    &::before {
      position: absolute;
      right: 0;
      bottom: 0;
      left: -11%;
      height: 8px;
      width: 120%;
      background: repeating-linear-gradient(
        -45deg,
        #f84a73 0,
        #f84a73 25%,
        transparent 0,
        transparent 50%,
        #3389ff 0,
        #3389ff 75%,
        transparent 0,
        transparent 100%
      );
      transform: skewX(-45deg);
      background-size: 100px;
      content: "";
    }
    .icon {
      width: 103px;
      @include flex;
      img {
        width: 52px;
        height: 52px;
      }
    }
    .user {
      flex: 1;
      .name {
        display: flex;
        margin-bottom: 23px;
        @include font-size(32);
        > div {
          line-height: 1;
          &:first-child {
            margin-right: 52px;
          }
        }
      }
      .address {
        color: $addressColor;
        @include font-size(26);
      }
    }
    .right-icon {
      padding: 0 42px 0 35px;
      img {
        height: 30px;
        width: 30px;
      }
    }
  }
  //商品列表
  .buy-order-info {
    .goods-list {
      li {
        @include flex;
        padding: 30px 0;
        .product {
          min-width: 200px;
          max-width: 200px;
          height: 200px;
          background: $bg;
          margin: 0 28px 0 30px;
          img {
            width: 100%;
            border-radius: 10px;
          }
        }
        .product-info {
          padding-right: 30px;
          @include iflex(space-between, flex-start, column nowrap);
          .name {
            @include text-column(2);
            line-height: 40px;
          }
          .spec {
            margin: 14px 0 12px;
            div {
              line-height: 1;
              color: $defaultGray;
              &:first-child {
                margin-bottom: 11px;
              }
            }
          }
          .price {
            width: 100%;
            @include flex(space-between);
            > div {
              &:first-child {
                @include font-size(30);
              }
              .van-stepper {
                transform: scale(0.8);
              }
            }
          }
        }
      }
    }
    .ext {
      padding-top: 30px;
      padding-bottom: 7px;
      li {
        @include flex(space-between);
        padding-bottom: 23px;
        div {
          line-height: 1;
        }
      }
    }
  }
  //支付类型
  .pay-type {
    .look-all {
      color: $p-AllColor;
      line-height: 1;
      height: 80px;
      @include flex;
      img {
        margin-left: 10px;
        width: 34px;
        height: 34px;
        &.bottom {
          transform: rotate(-180deg);
        }
      }
    }
  }
  .discount {
    @include iflex;
    .price-tag {
      line-height: 1;
      padding: 12px 30px;
      background: $priceColor;
      border-radius: 23px;
      color: #fff;
      @include font-size(24);
    }
    img {
      width: 34px;
      height: 34px;
      margin-left: 20px;
    }
  }
  .fixed-btn {
    height: 120px;
    position: relative;
    padding-bottom: constant(safe-area-inset-bottom);
    padding-bottom: env(safe-area-inset-bottom);
    background: $bg;

    .van-submit-bar__bar {
      height: 120px;
    }
    .van-submit-bar__text {
      color: $defaultFontColor;
    }
    .van-submit-bar__price {
      color: $priceColor;
    }
    .van-submit-bar__button--danger {
      background: $priceColor;
      width: 220px;
      height: 120px;
      border-radius: 0;
      position: relative;
      right: -32px;
    }
  }
}
</style>
